@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.title-font {
  font-family: "Roboto", sans-serif; }

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

.togglebutton {
  width: 4rem !important;
  height: 2.5rem !important; }
  .togglebutton svg {
    height: 80%; }

.togglebutton:focus {
  outline: none !important; }

.column-title .column-title-text {
  font-size: 3rem;
  font-weight: 900; }

.column-title .column-spot {
  font-size: 2rem; }
  .column-title .column-spot span {
    font-weight: 600; }

.hey .social-buttons .social-button {
  padding: 0.2rem; }

.what .column-title .column-title-text {
  font-size: 2.5rem; }

.what .card {
  border: none;
  box-shadow: 0 0.5em 1em -0.125em rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.02); }
  .what .card .card-text-wrapper {
    height: 5rem; }
    .what .card .card-text-wrapper .card-text {
      color: #4a4a4a; }
  .what .card .btn {
    font-size: 0.8rem; }
    .what .card .btn i span {
      font-family: "Roboto", sans-serif;
      font-weight: 100; }

.about .column-title .column-title-text {
  font-size: 2.5rem; }

.about .skill-list-group {
  line-height: 1.7; }
  .about .skill-list-group li {
    margin-bottom: 1.5rem; }
    .about .skill-list-group li h4 {
      font-size: 1.3rem; }

.me .column-title .column-title-text {
  font-size: 2.5rem; }

.me .social-buttons .mail-button .mail {
  border-bottom: 5px solid #007bff; }

.me .social-buttons :hover {
  text-decoration: none !important; }

@media (min-width: 320px) and (max-width: 480px) {
  .togglebutton {
    width: 3.3rem;
    height: 2.2rem; }
  .skill-list-group li h4 {
    font-size: 1rem;
    padding-right: 3rem; }
  .me .column-title .column-spot {
    font-size: 1.5rem; }
  .me .social-buttons {
    font-size: 13px; }
    .me .social-buttons .mail-button h3 {
      font-size: 1.05rem; } }

